.navbar {
    text-align: center;
    display: grid;
    grid-template-rows: 6fr 1fr;
    grid-template-columns: 1fr 7fr 1fr;
    grid-template-areas: 
        
        ". sidebar ."
        ". ....... .";
    list-style: none;
    height: auto;
    width: 100%;
   
    background-color: #f2f4f7;
    color: black;

    
}
.ulbox{
    
    border: 1px solid #e7e9eb;
    background-color: white;
    grid-area: sidebar;

}
.ulstyle {
    padding-left: 0;
    list-style-type: none;
   
}

.ulstyle li {
    border-bottom: 1px solid #e7e9eb;
    font-size: 1.3rem;
    padding-bottom: 8px;
}

a {
    color: black;
    text-decoration: none;
}

.logo {
    align-self: center;
    grid-area: logo;
   
}
@media screen and (min-width: 760px){

    .navbar {
        text-align: center;
        display: grid;
        grid-template-rows: 6fr 1fr;
        grid-template-columns: 1fr 7fr 1fr;
        grid-template-areas: 
            
            ". sidebar ."
            ". ....... .";
        list-style: none;
        height: 100vh;
       
        background-color: #f2f4f7;
        color: black;
    
        
    }
    .ulbox{
        margin: 5vh;
        padding: 5vw;
        border: 1px solid #e7e9eb;
        background-color: white;
        grid-area: sidebar;
    
    }
    .ulstyle {
        padding-left: 0;
        list-style-type: none;
       
    }
    
    .ulstyle li {
        border-bottom: 1px solid #e7e9eb;
        font-size: 1.3rem;
        padding-bottom: 8px;
    }
    
    a {
        color: black;
        text-decoration: none;
    }
    
    .logo {
        align-self: center;
        grid-area: logo;
       
    }
}